/* UI */
.no-webp .inicio {
  background-image: url(/build/img/header.jpg);
}

.webp .inicio {
  background-image: url(/build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  /* en la seccion de header tenemos la clase header y tambien la clase inicio
  en el mismo elemento. Para hacer referencia al mismo elemento, le colocamos un
  amperson... */
}
.header.inicio {
  /* para alinear la imagen y que esté centrada */
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  /* coloca el parrafo a la derecha de la navegacion automaticamente. Para 
  solucionarlo le colocamos un... */
  flex-direction: column;
  justify-content: space-between;
  /*  este space coloca el texto en la parte inferior del header */
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 1.5rem;
  background-color: #333333;
  margin-bottom: 0;
  margin-top: 2rem;
}
.footer .contenido-footer {
  padding-top: 5rem, 2;
}
.footer .navegacion {
  opacity: 1;
  visibility: visible;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  /*===EFECTO EN LA IMAGEN DE DARK MODE===*/
  filter: invert(100%);
  /* con esto cambiamos el color del svg DARKMODE */
}
.derecha .dark-mode:hover {
  cursor: pointer;
  /* finalmente colocamos este hover para que sea un boton */
}
@media (min-width: 768px) {
  .derecha .dark-mode {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  /* desde el momento que agregamos el display flex, coloca los elementos uno 
  junto al otro */
  flex-direction: column;
  /* de abajo hacias arriba */
  align-items: center;
  /* hasta acá el logo esta grande y arriba de la navegacion, lo solucionamos con un mixin... */
  padding-top: 3rem;
  /* hace que se muetren iguales */
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    /* horizontal en un dispositivo mas grande y vertical en 
    dispositivos pequeños, sin embargo la navegacion está junto al logo...*/
    justify-content: space-between;
    /* esto hará que la navegacion se vaya al lado derecho
    y el logo al lado izquierdo */
    align-items: flex-end;
  }
}

.logo {
  height: 5rem;
  width: 40rem;
  padding-top: 0;
  padding-bottom: 0;
}

.navegacion {
  height: 0;
  /* ==ANIMAR EL MENU DE HAMBURGUESA== */
  visibility: hidden;
  /* hará que se no vea la navegacion */
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  /* display: none;  */
  /* ==FIN DE ANIMACION...CONTINUA EN (.mostrar)== */
  /* enlace */
}
@media (min-width: 768px) {
  .navegacion {
    /* se insertan en columnas a la derecha, lo solucionamos con... */
    display: flex;
    /* para alinear el logo con la navegacion colocamos un align-items... */
    align-items: center;
    /* centra los elementos verticalmente */
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  /* toman todo el ancho disponible */
  text-align: center;
  /* centra los elementos... 

  hasta aca la navegacion se mantiene de de arriba hacia abajo...*/
  /* este include separa los enlaces de la navegacion */
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    /* para separar las palabras de la navegacion */
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    /* elimina el margin del ultimo elemento de la navegacion */
    margin-right: 0;
  }
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

/*=======AL DAR CLICK AL MENU DE HAMBURGUESA SE MODIFICARÁ EL BLOCK=======*/
.mostrar {
  /* NO SE PUEDE DAR ANIMACIONES A LOS DISPLAY (nos vamos a .navegacion)

      ===CON ESTO SOLUCIONAMOS EL DETALLE DE LA NAVEGACION, QUE SE MUESTRA DE ARRIBA A ABAJO DEN UNA TABLET===
  display: flex;
     flex-direction: column;

     @include tablet {
         flex-direction: row;
  ===HASTA QUE SE SOLUCIONA EL PROBLEMA DE LA NAVEGACION=== 
     } */
  visibility: visible;
  /* hara que se vea la navegacion */
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
